.home {
  padding: 3rem 2rem;
}

.home-hero {
  margin-bottom: 4rem;
}

.home-hero-grid {
  display: grid;
  grid-template-columns: 1.2fr 0.8fr;
  align-items: center;
  gap: 3rem;
}

.home-title {
  font-size: 2.4rem;
  margin-bottom: 1rem;
}

.home-subtitle {
  font-size: 1.1rem;
  opacity: 0.9;
  margin-bottom: 2rem;
  max-width: 520px;
}

.home-actions {
  display: flex;
  gap: 1rem;
}

.home-hero-image {
  display: flex;
  justify-content: center;
}

.home-hero-image img {
  max-width: 260px;
  border-radius: 12px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.35);
}

/* FEATURES */
.home-features {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 2rem;
  margin-bottom: 4rem;
}

.feature-card {
  background: rgba(255, 255, 255, 0.03);
  padding: 1.5rem;
  border-radius: 14px;
  text-align: center;
}

/* ABOUT */
.home-about {
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
}

/* RESPONSIVE */
@media (max-width: 768px) {
  .home-hero-grid {
    grid-template-columns: 1fr;
    text-align: center;
  }
  .home-actions {
    justify-content: center;
  }
}